import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Eye, DollarSign, ArrowDownLeft, ArrowRightLeft, Clock } from 'lucide-react';
import { ProfitService } from '@/utils/profitService';
import { ProfitPinDialog } from './ProfitPinDialog';
import { formatCurrency } from '@/utils/formatters';
import { reportsService } from '@/utils/reportsService';

interface DailyProfitDisplayDialogProps {
  userId?: string;
  transactions: any[];
}

interface ProfitData {
  dailyWithdrawalProfit: number;
  dailyTransferProfit: number;
  lastUpdated: Date;
}

const DailyProfitDisplayDialog: React.FC<DailyProfitDisplayDialogProps> = ({ userId, transactions }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isPinDialogOpen, setIsPinDialogOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [profitData, setProfitData] = useState<ProfitData>({
    dailyWithdrawalProfit: 0,
    dailyTransferProfit: 0,
    lastUpdated: new Date()
  });

  useEffect(() => {
    if (userId && isOpen && isAuthenticated) {
      loadProfitData();
    }
  }, [userId, isOpen, isAuthenticated, transactions]);

  const loadProfitData = async () => {
    if (!userId) return;
    
    try {
      //            localStorage
      if (Array.isArray(transactions) && transactions.length > 0) {
        const today = new Date();
        const visibleDaily = reportsService.filterVisibleTransactions(transactions as any[], 'daily', userId);

        let dailyWithdrawProfit = 0;
        let dailyTransferProfit = 0;

        visibleDaily.forEach((tx: any) => {
          const txDate = new Date(tx.createdAt);
          const status = String(tx.status || 'confirmed').toLowerCase();
          if (txDate.toDateString() !== today.toDateString()) return;
          //    (pending)    
        
          const t = (tx.type || tx.txnType || '').toLowerCase();
          const title = String(tx.title || '');
          if (t === 'cash_addition' || title.includes('  ')) return;
          const txForCalc = {
            type: t === 'withdrawal' ? 'withdraw' : t,
            amount: Number(tx.amount || 0),
            commission: tx.commission,
          };

          if (txForCalc.type === 'withdraw') {
            dailyWithdrawProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          } else if (txForCalc.type === 'send' || txForCalc.type === 'receive' || txForCalc.type === 'transfer') {
            dailyTransferProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          }
        });

        setProfitData({
          dailyWithdrawalProfit: Math.round(dailyWithdrawProfit * 100) / 100,
          dailyTransferProfit: Math.round(dailyTransferProfit * 100) / 100,
          lastUpdated: new Date()
        });

        //         
        try { ProfitService.updateProfitsFromTransactions(transactions, userId); } catch {}
        return;
      }

      //        localStorage ()
      const profits = ProfitService.getWithdrawTransferProfits(userId);
      setProfitData({
        dailyWithdrawalProfit: profits.dailyWithdrawProfit || 0,
        dailyTransferProfit: profits.dailyTransferProfit || 0,
        lastUpdated: new Date()
      });
    } catch (error) {
      console.error('Error loading profit data:', error);
    }
  };

  const formatCurrency = (amount: number) => {
    return `${amount.toFixed(2)} .`;
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('ar-EG', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  const handleEyeClick = () => {
    if (ProfitService.hasProfitPin(userId)) {
      setIsPinDialogOpen(true);
    } else {
      setIsPinDialogOpen(true);
    }
  };

  const handlePinSuccess = () => {
    setIsAuthenticated(true);
    setIsOpen(true);
    setIsPinDialogOpen(false);
  };

  return (
    <>
      <Button
        size="sm"
        variant="ghost"
        onClick={handleEyeClick}
        className="h-2 w-2 p-0 bg-gradient-to-r from-blue-100 to-blue-200 text-blue-700 hover:from-blue-200 hover:to-blue-300 hover:text-blue-800 rounded transition-all duration-300 hover:scale-110 shadow-sm hover:shadow-md"
        title="  "
      >
        <Eye className="h-1.5 w-1.5" />
      </Button>

      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="max-w-[220px] mx-auto bg-white border border-gray-200 shadow-md rounded sm:max-w-[180px] sm:bg-gradient-to-br sm:from-blue-50 sm:to-white sm:border-2 sm:border-blue-200 sm:shadow-2xl sm:rounded-2xl">
          <DialogHeader className="text-center pb-0 border-b-0 sm:pb-2 sm:border-b sm:border-blue-200">
            <DialogTitle className="text-xs font-bold text-blue-800 flex items-center justify-center gap-0 mb-0 leading-tight tracking-tight sm:text-sm sm:gap-1 sm:mb-0 sm:leading-normal sm:tracking-normal">
              <DollarSign className="h-2 w-2 text-blue-600 sm:h-3 sm:w-3" />
              
            </DialogTitle>
          </DialogHeader>

          {/*       */}
          <div className="py-0 sm:py-3">
            <div className="rounded-xl border border-blue-300 bg-white overflow-hidden">
              {/*  */}
              <div className="p-2 sm:p-3 bg-red-50 border-b border-blue-100">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-1">
                    <ArrowDownLeft className="h-3 w-3 text-red-600" />
                    <span className="font-medium text-red-800 text-xs"></span>
                  </div>
                  <span className="font-bold text-sm text-red-900 bg-white px-2 py-0.5 rounded">
                    {formatCurrency(profitData.dailyWithdrawalProfit)}
                  </span>
                </div>
              </div>

              {/*  */}
              <div className="p-2 sm:p-3 bg-green-50 border-b border-blue-100">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-1">
                    <ArrowRightLeft className="h-3 w-3 text-green-600" />
                    <span className="font-medium text-green-800 text-xs"></span>
                  </div>
                  <span className="font-bold text-sm text-green-900 bg-white px-2 py-0.5 rounded">
                    {formatCurrency(profitData.dailyTransferProfit)}
                  </span>
                </div>
              </div>

              {/*  */}
              <div className="p-2 sm:p-3 bg-blue-50">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-1">
                    <DollarSign className="h-3 w-3 text-blue-600" />
                    <span className="font-medium text-blue-800 text-xs"></span>
                  </div>
                  <span className="font-bold text-sm text-blue-900 bg-white px-2 py-0.5 rounded">
                    {formatCurrency(profitData.dailyWithdrawalProfit + profitData.dailyTransferProfit)}
                  </span>
                </div>
              </div>
            </div>
          </div>

          <div className="flex justify-center pt-0 border-t-0 sm:pt-2 sm:border-t sm:border-blue-200">
            <Button
              onClick={() => setIsOpen(false)}
              className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded-xl text-sm sm:bg-gradient-to-r sm:from-blue-600 sm:to-blue-700 sm:hover:from-blue-700 sm:hover:to-blue-800 sm:px-4 sm:py-1 sm:rounded-xl sm:shadow-lg sm:hover:shadow-xl sm:transition-all sm:duration-300 sm:text-xs"
            >
              
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <ProfitPinDialog
        open={isPinDialogOpen}
        onOpenChange={setIsPinDialogOpen}
        onSuccess={handlePinSuccess}
        userId={userId}
      />
    </>
  );
};

export const DailyProfitEyeButton: React.FC<DailyProfitDisplayDialogProps> = (props) => {
  return <DailyProfitDisplayDialog {...props} />;
};

export default DailyProfitDisplayDialog;
