import React, { useState, useEffect } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { Eye, DollarSign, ArrowDownLeft, ArrowRightLeft, Clock, Calendar } from 'lucide-react';
import { ProfitService } from '@/utils/profitService';
import { reportsService } from '@/utils/reportsService';
import { MasterPinDialog } from './MasterPinDialog';
import { toast } from '@/hooks/use-toast'
import { getUserSubscription, SubscriptionPlan } from '@/utils/subscriptionService'
import { useFirebaseAuth } from '@/contexts/FirebaseAuthContext'

interface MonthlyProfitDisplayDialogProps {
  userId?: string;
  transactions: any[];
}

interface ProfitData {
  monthlyWithdrawalProfit: number;
  monthlyTransferProfit: number;
  lastUpdated: Date;
}

const MonthlyProfitDisplayDialog: React.FC<MonthlyProfitDisplayDialogProps> = ({ userId, transactions }) => {
  const [isOpen, setIsOpen] = useState(false);
  const [isMasterPinDialogOpen, setIsMasterPinDialogOpen] = useState(false);
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [profitData, setProfitData] = useState<ProfitData>({
    monthlyWithdrawalProfit: 0,
    monthlyTransferProfit: 0,
    lastUpdated: new Date()
  });
  const { userSubscription } = useFirebaseAuth();

  useEffect(() => {
    if (userId && isOpen && isAuthenticated) {
      loadProfitData();
    }
  }, [userId, isOpen, isAuthenticated]);

  const loadProfitData = async () => {
    if (!userId) return;
    
    try {
      //            localStorage
      if (Array.isArray(transactions) && transactions.length > 0) {
        const today = new Date();
        const currentMonth = today.getMonth();
        const currentYear = today.getFullYear();

        const visibleMonthly = reportsService.filterVisibleTransactions(transactions as any[], 'monthly', userId);

        let monthlyWithdrawProfit = 0;
        let monthlyTransferProfit = 0;

        visibleMonthly.forEach((tx: any) => {
          const txDate = new Date(tx.createdAt);
          const status = String(tx.status || 'confirmed').toLowerCase();
          if (txDate.getMonth() !== currentMonth || txDate.getFullYear() !== currentYear) return;
          if (status !== 'completed' && status !== 'confirmed') return;

          const t = (tx.type || tx.txnType || '').toLowerCase();
          const txForCalc = {
            type: t === 'withdrawal' ? 'withdraw' : t,
            amount: Number(tx.amount || 0),
            commission: tx.commission,
          };

          if (txForCalc.type === 'withdraw') {
            monthlyWithdrawProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          } else if (txForCalc.type === 'send' || txForCalc.type === 'receive' || txForCalc.type === 'transfer') {
            monthlyTransferProfit += ProfitService.calculateProfitFromTransaction(txForCalc);
          }
        });

        setProfitData({
          monthlyWithdrawalProfit: Math.round(monthlyWithdrawProfit * 100) / 100,
          monthlyTransferProfit: Math.round(monthlyTransferProfit * 100) / 100,
          lastUpdated: new Date()
        });
        return;
      }

      //        localStorage ()
      const profits = ProfitService.getWithdrawTransferProfits(userId);
      setProfitData({
        monthlyWithdrawalProfit: profits.monthlyWithdrawProfit || 0,
        monthlyTransferProfit: profits.monthlyTransferProfit || 0,
        lastUpdated: new Date()
      });
    } catch (error) {
      console.error('Error loading profit data:', error);
    }
  };

  const formatCurrency = (amount: number) => {
    return `${amount.toFixed(2)} .`;
  };

  const formatDate = (date: Date) => {
    return new Intl.DateTimeFormat('ar-EG', {
      year: 'numeric',
      month: 'long',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    }).format(date);
  };

  //         
  const isZeroPlan = async (): Promise<boolean> => {
    try {
      const localSub: any = (userSubscription as any)?.subscription || null;
      const rawLocal: any = localSub?.planType ?? localSub?.plan ?? null;
      const planLocalStr = typeof rawLocal === 'string' ? rawLocal.toLowerCase() : null;
      if (rawLocal === SubscriptionPlan.ZERO || planLocalStr === 'zero' || planLocalStr === '0' || rawLocal === 0) {
        return true;
      }
    } catch {}

    try {
      if (userId) {
        const remoteSub: any = await getUserSubscription(userId);
        const rawRemote: any = remoteSub?.planType ?? remoteSub?.plan ?? null;
        const planRemoteStr = typeof rawRemote === 'string' ? rawRemote.toLowerCase() : null;
        if (rawRemote === SubscriptionPlan.ZERO || planRemoteStr === 'zero' || planRemoteStr === '0' || rawRemote === 0) {
          return true;
        }
      }
    } catch {}

    return false;
  };

  const handleEyeClick = async () => {
    //         
    try {
      if (await isZeroPlan()) {
        toast({
          title: ' ',
          description: '       .     .',
          variant: 'destructive'
        })
        return
      }
    } catch {}
    setIsMasterPinDialogOpen(true)
  }

  const handlePinSuccess = async () => {
    //            
    try {
      if (await isZeroPlan()) {
        toast({
          title: ' ',
          description: '       .     .',
          variant: 'destructive'
        });
        setIsMasterPinDialogOpen(false);
        return;
      }
    } catch {}

    setIsAuthenticated(true);
    setIsOpen(true);
    setIsMasterPinDialogOpen(false);
  };

  return (
    <>
      <Button
        size="sm"
        variant="ghost"
        onClick={handleEyeClick}
        className="h-2 w-2 p-0 bg-gradient-to-r from-blue-100 to-blue-200 text-blue-700 hover:from-blue-200 hover:to-blue-300 hover:text-blue-800 rounded transition-all duration-300 hover:scale-110 shadow-sm hover:shadow-md"
        title="  "
      >
        <Eye className="h-1.5 w-1.5" />
      </Button>

      <Dialog open={isOpen} onOpenChange={setIsOpen}>
        <DialogContent className="max-w-[220px] mx-auto bg-white border border-gray-200 shadow-md rounded sm:max-w-[180px] sm:bg-gradient-to-br sm:from-purple-50 sm:to-white sm:border-2 sm:border-purple-200 sm:shadow-2xl sm:rounded-2xl">
          <DialogHeader className="text-center pb-0 border-b-0 sm:pb-2 sm:border-b sm:border-purple-200">
            <DialogTitle className="text-xs font-bold text-purple-800 flex items-center justify-center gap-0 mb-0 leading-tight tracking-tight sm:text-xs sm:gap-1 sm:mb-0 sm:leading-normal sm:tracking-normal">
              <Calendar className="h-2 w-2 text-purple-600 sm:h-3 sm:w-3" />
              
            </DialogTitle>
          </DialogHeader>

          {/*       */}
          <div className="py-0 sm:py-3">
            <div className="rounded-xl border border-purple-300 bg-white overflow-hidden">
              {/*  */}
              <div className="p-2 sm:p-3 bg-red-50 border-b border-purple-100">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-1">
                    <ArrowDownLeft className="h-3 w-3 text-red-600" />
                    <span className="font-medium text-red-800 text-xs"></span>
                  </div>
                  <span className="font-bold text-sm text-red-900 bg-white px-2 py-0.5 rounded">
                    {formatCurrency(profitData.monthlyWithdrawalProfit)}
                  </span>
                </div>
              </div>

              {/*  */}
              <div className="p-2 sm:p-3 bg-blue-50 border-b border-purple-100">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-1">
                    <ArrowRightLeft className="h-3 w-3 text-blue-600" />
                    <span className="font-medium text-blue-800 text-xs"></span>
                  </div>
                  <span className="font-bold text-sm text-blue-900 bg-white px-2 py-0.5 rounded">
                    {formatCurrency(profitData.monthlyTransferProfit)}
                  </span>
                </div>
              </div>

              {/*  */}
              <div className="p-2 sm:p-3 bg-purple-50">
                <div className="flex items-center justify-between">
                  <div className="flex items-center gap-1">
                    <DollarSign className="h-3 w-3 text-purple-600" />
                    <span className="font-medium text-purple-800 text-xs"></span>
                  </div>
                  <span className="font-bold text-sm text-purple-900 bg-white px-2 py-0.5 rounded">
                    {formatCurrency(profitData.monthlyWithdrawalProfit + profitData.monthlyTransferProfit)}
                  </span>
                </div>
              </div>
            </div>
          </div>

          {/*      */}
          {!isAuthenticated && (
            <div className="absolute inset-0 bg-yellow-200/70 backdrop-blur-sm flex items-center justify-center rounded sm:rounded-2xl border border-yellow-300 pointer-events-auto">
              <Button
                size="sm"
                variant="ghost"
                onClick={() => setIsMasterPinDialogOpen(true)}
                className="p-2 bg-yellow-300/80 text-yellow-900 hover:bg-yellow-400/90 rounded-full shadow-sm hover:shadow-md transition-all duration-300"
                title="   "
              >
                <Eye className="h-4 w-4" />
              </Button>
            </div>
          )}

          <div className="flex justify-center pt-0 border-t-0 sm:pt-2 sm:border-t sm:border-purple-200">
            <Button
              onClick={() => setIsOpen(false)}
              className="bg-purple-600 hover:bg-purple-700 text-white px-6 py-2 rounded-xl text-sm sm:bg-gradient-to-r sm:from-purple-600 sm:to-purple-700 sm:hover:from-purple-700 sm:hover:to-purple-800 sm:px-4 sm:py-1 sm:rounded-xl sm:shadow-lg sm:hover:shadow-xl sm:transition-all sm:duration-300"
            >
              
            </Button>
          </div>
        </DialogContent>
      </Dialog>

      <MasterPinDialog
        open={isMasterPinDialogOpen}
        onOpenChange={setIsMasterPinDialogOpen}
        onSuccess={handlePinSuccess}
        description="          "
      />
    </>
  );
};

export const MonthlyProfitEyeButton: React.FC<MonthlyProfitDisplayDialogProps> = (props) => {
  return <MonthlyProfitDisplayDialog {...props} />;
};

export default MonthlyProfitDisplayDialog;