                  <div className="flex items-center gap-2 mb-2">
                    <Button variant={isInvoiceMode ? 'default' : 'outline'} size="sm" onClick={() => setIsInvoiceMode(v => !v)}>
                      {isInvoiceMode ? ' : ' : '  '}
                    </Button>
                    <Button size="sm" className="bg-indigo-600 hover:bg-indigo-700 text-white" disabled={Object.keys(invoiceSelections).filter(id => (invoiceSelections[id] || 0) > 0).length === 0} onClick={printSelectedInvoice}>
                       
                    </Button>
                  </div>
